import type { Board, Field, FieldKey } from "./types";

export function onFieldClick(board:Board, fieldKey:FieldKey) {
    const field = board.map.get(fieldKey)

    if (!field) {
        return
    }

    if (field.color) {
        return
    }

    field.color = board.turn

    board.turn = board.turn === "x" ? "o" : "x"

    board.map.set(fieldKey, field)
    renderField(field)
}


export function renderField(field: Field) {
    if(field.element) {
        if(field.color) {
            field.element.dataset.color = field.color
            field.element.innerHTML = `<div class="icon-${field.color === "x" ? "x" : "circle"}"></div>` 
        } else {
            delete field.element.dataset.color
            field.element.innerHTML = ""
        }
    }
}