import { boardIterator } from "./board";
import { renderField } from "./field";
import type { Board, Game } from "./types";

export function renderGame(board: Board, game: Game) {
    let i = 0
    for (const { field } of boardIterator(game.board)) {
        if (field.toLocaleLowerCase() === "x") {
            i++
        } else if (field.toLocaleLowerCase() === "o") {
            i--
        }
    }

    if (i < 0) {
        // x turn
        board.turn = "x"
    } else {
        // o turn
        board.turn = "o"
    }

    for (const [key, field] of board.map.entries()) {
        const [row, col] = key.split(":").map(Number)
        const boardField = game.board[row]?.[col]

        const newField = {
            ...field,
            color: boardField.toLowerCase() as "x" | "o" || null
        }

        board.map.set(key, newField)
        renderField(newField)
    }
}
