import { getGame, searchGames } from "../api/game";
import { boardIterator } from "./board";
import { initializeBoard } from "./initialize-board";
import { renderGame } from "./render-game";
import type { Board, Game } from "./types";
import {onGameSearchFormChange} from"./search"

export {onGameSearchFormChange}
const board = new Proxy({
  map: new Map(),
  element: document.getElementById("board") as HTMLElement,
  turn: "x"
} as Board, {})

initializeBoard(board)

