const searchInput = document.getElementById("game-search-input") as HTMLInputElement

searchInput.addEventListener("input", () => {

})


const searchResultsEl = document.getElementById("game-search-results") as HTMLElement

// id, game
const gameCache = new Map<string, (Game & { loading: false }) | { loading: Promise<false> }>()

function findCard(el: HTMLElement): HTMLElement | null {
  if (el.dataset.searchId !== undefined) {
    return el
  }

  if (el.parentElement === null) {
    return null
  }

  return findCard(el.parentElement)
}

searchResultsEl.addEventListener("mousedown", async (e) => {
  const el = e.target as HTMLElement

  const cardEl = findCard(el)

  if (!cardEl) {
    return
  }

  const gameId = cardEl.dataset.searchId

  if (!gameId) {
    return
  }

  let game = gameCache.get(gameId)

  if (!game) {
    game = await getGame(gameId)
  }

  if (game?.loading instanceof Promise) {
    await game.loading
    game = gameCache.get(gameId)
  }


  if (game?.loading === false) {
    renderGame(board, game)
  }


})


searchResultsEl.addEventListener("mouseover", (e) => {
  const cardEl = findCard(e.target as HTMLElement)

  // prefetch

  if (!cardEl) {
    return
  }

  const gameId = cardEl.dataset.searchId

  if (!gameId) {
    return
  }

  const game = gameCache.get(gameId)

  if (game) {
    return
  }

  const res = getGame(gameId)

  gameCache.set(gameId, { loading: res.then(() => (false)) })

  res.then((game) => {
    gameCache.set(gameId, { ...game, loading: false })
  })
})
