const el:{query: HTMLInputElement | null} = {
    query: null
}

function init() {
    if(el.query === null) {
        el.query = document.getElementById("game-search-input") as HTMLInputElement
    }
    
}

/**
 * gets invoked from many places, whenever the search inputs change
 * native: onKeyDown, onChange etc.
 */
export function onGameSearchFormChange() {
  init()

  const request = {
    query: el.query?.value,
  }

  console.log(request);
  
}